import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TambolasocketService {

  private socket = io("http://192.168.2.11:5000/");
  constructor() { }

  hostCreate(){
    this.socket.emit('hostCreateGame');
  }
  newRoomCreated(){
    let observable = new Observable<{gameId:String}>(observer=>{
      this.socket.on('newGameCreated', data=>{
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  newPlayerJoined(){
    let observable = new Observable<{id:String,gameId:String,user:String,playerNotify:String,playerTicketCount:String}>(observer=>{
      this.socket.on('newUser', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  joinPlayer=(data)=>{
    console.log(data);
    this.socket.emit("playerJoinGame", data , (error)=>{
      if(error){
        alert(error);
      }
    });
  }
  sendHostReq=(data)=>{
    this.socket.emit("playerTicketReq",data);
  }
  startGame=(gameConfig)=>{
    this.socket.emit("startGame",gameConfig);
  }
  sendHostTicketMessage(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('sendHostReq', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  categoriesReceived(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('gameCategories', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  ticketRecieved(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('tickets', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  hostTixConfirm(tixCount){
    this.socket.emit('hostTixConfirm',tixCount);
  }
  sendTicketForVerification=(ticketData)=>{
    console.log(ticketData);
    this.socket.emit('ticketVerify',ticketData);
  }
  ticketToHost(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('ticketToHost', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  sendDrawNumber=numberObj=>{
    this.socket.emit('drawNumber',numberObj);
  }
  receiveNumber(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('newNumber', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  prizeWinner(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('prizeWinner', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  resultList(){
    let observable = new Observable<any>(observer=>{
      this.socket.on('resultList', data=>{
        console.log(data);
        observer.next(data);
      });
      return()=> this.socket.disconnect();
    });
    return observable;
  }
  verifyResult=verifyData=>{
    this.socket.emit('verifyResult', verifyData);
  }
  shareResult=winnerList=>{
    this.socket.emit('shareResult', winnerList);
  }
}
