import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA} from '@angular/material/dialog';
import { TambolasocketService } from 'src/app/services/tambolasocket.service';

@Component({
  selector: 'app-ticket-image',
  templateUrl: './ticket-image.component.html',
  styleUrls: ['./ticket-image.component.css']
})
export class TicketImageComponent implements OnInit {

    ticketImage: String;
    ticketUser: String;
    claimPrize: String;
    userId: String;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
  private tambolaService: TambolasocketService
  ) {
   }

  ngOnInit() {
    console.log(this.data);
    this.ticketImage=this.data["ticketImage"];
    this.ticketUser=this.data["ticketUser"];
    this.claimPrize=this.data["claimPrize"];
    this.userId=this.data["ticketUserId"];
  }
  verifyResult=()=>{
    this.tambolaService.verifyResult({
      gameId: sessionStorage.getItem('gameId'),
      ticketUserId: this.userId,
      winPrize: this.claimPrize,
      ticketUser: this.ticketUser
    })
  }

}
